import Theme from '@/components/repeat_coms/Theme.vue';

export default {
	onShow() {
		this.getStorage();
	},
	data() {
		return {
			hotList: ["火影忍者", "斗破苍穹", "末世", "龙王传说", "玄幻", "都市", "科幻"],
			historyList: [],
			keyword: "",
		}
	},
	components: {
		Theme
	},
	methods: {
		getStorage() {
			let value = uni.getStorageSync('historyList_search');
			if (value) {
				this.historyList = value;
			} else {
				this.historyList = []
			}
		},
		input(e) {//监听输入内容
			this.keyword = e.detail.value.trim();
		},
		search() {
			if(this.historyList.indexOf(this.keyword) == -1) {
				this.historyList.push(this.keyword);
				uni.setStorage({ //本地保存
					key: 'historyList_search',
					data: this.historyList
				});
			}
			this.keyword = '';
		},
		delect_keyword() {
			this.keyword = ""
		},
		delect_historyList() {
			this.historyList = [];
			uni.removeStorageSync('historyList_search');
		},
		delect_search_item(i) {
			this.historyList.splice(i, 1);
			uni.setStorage({ //本地保存
				key: 'historyList_search',
				data: this.historyList
			})
		}
	}
}
